import React from "react";
import "./Teamshart.scss"

const Teamshart = (props) => {
    return (
        <div className="team-item">
            <img src={props.imgTeam} alt="" className="team-logo"></img>
            <p className="team-text">{props.text}</p>
        </div>
    )
}

export default Teamshart;